import * as React from 'react';
import { Routes, Route } from 'react-router-dom';

import { SharedDataCtx } from '@miq/contextjs';

const ShopStaffIndexView = React.lazy(() => import('./IndexView'));

const SupplierOrderIndexView = React.lazy(() => import('./Supplier'));
const SupplierOrderProductUpdateView = React.lazy(() => import('./Supplier/OrderProductUpdateView'));
const SupplierOrderUpdateView = React.lazy(() => import('./Supplier/OrderUpdateView'));

const CategoryIndexView = React.lazy(() => import('./Category'));
const CategoryUpdateView = React.lazy(() => import('./Category/UpdateView'));

const ProductIndexView = React.lazy(() => import('./Product/IndexView'));
const ShopSettingUpdateView = React.lazy(() => import('./Setting'));

export default function ShopStaffRoutes() {
  const ctx = React.useContext(SharedDataCtx);
  const { shopy_settings } = ctx;

  if (!shopy_settings) return null;

  return (
    // <SharedDataProvider>
    <Routes>
      <Route
        path="orders/*"
        element={
          <Routes>
            <Route path={`:ordSlug/:prodSlug/`} element={<SupplierOrderProductUpdateView />} />
            <Route path={`:ordSlug/`} element={<SupplierOrderUpdateView />} />
            <Route path="/*" element={<SupplierOrderIndexView />} />
          </Routes>
        }
      />

      <Route
        path="products/*"
        element={
          <Routes>
            <Route path="/*" element={<ProductIndexView />} />
          </Routes>
        }
      />

      <Route
        path="categories/*"
        element={
          <Routes>
            <Route path=":catSlug/" element={<CategoryUpdateView />} />
            <Route index element={<CategoryIndexView />} />
          </Routes>
        }
      />

      <Route path="settings/" element={<ShopSettingUpdateView />} />
      <Route path="/" element={<ShopStaffIndexView />} />
    </Routes>
    // </SharedDataProvider>
  );
}

export * from './types';
