import { ImgType } from '@miq/componentjs';
import { TextChoicesType } from '@miq/formjs';
import { WithMetaFields } from '@miq/utiljs';

export type PriceDataType = { currency: string; amount: string; amountWithSymbol: string };
export type PriceType = number | PriceDataType;

export type ProductType = WithMetaFields<{
  slug?: string;

  category?: string;
  category_data?: CategoryType;
  name?: string;

  cover?: string;
  cover_data?: ImgType;
  images?: string[];
  images_data?: ImgType[];

  readonly name_truncated?: string;
  description?: string;
  retail_price?: PriceType;
  retail_price_data?: PriceDataType;
  is_on_sale?: boolean;
  sale_price?: PriceType;
  sale_price_data?: PriceDataType;
  is_pre_sale?: boolean;
  is_pre_sale_text?: string;
  is_pre_sale_dt?: string | Date;
  url?: string;

  has_attributes?: boolean;
  attributes?: any[];
  sizes?: any[];
  size_count?: number;

  is_published?: boolean;
  dt_published?: string | Date;

  supplier_item: SupplierItemType;
  position?: number;
  color_group_pk?: string;
  stage?: string;

  readonly detail_path?: string;

  created?: string | Date;
  updated?: string | Date;
}>;

export type CategoryChoicesType = {
  items: TextChoicesType;
};

export type CategoryType = WithMetaFields<{
  slug?: string;
  supplier_item_id?: string;
  name?: string;
  description?: string;
  cover?: ImgType;
  position?: number;

  is_published?: boolean;
  dt_published?: string | Date;

  cover_data?: ImgType;
  products_count?: number;
  published_count?: number;
  draft_count?: number;
  readonly detail_path?: string;

  created?: string | Date;
  updated?: string | Date;
}>;

export type SupplierItemType = {
  slug?: string;
  item_id?: string;
  item_sn?: string;
  category?: string;
  url?: string;
  cost?: number;
  created?: string;
  updated?: string;
};

export enum SupplierName {
  SHEIN = 'SHEIN',
  PLT = 'PLT',
  FNOVA = 'FNOVA',
}

export type SupplierOrderType = {
  slug?: string;
  name?: string;
  supplier?: string;
  order_id?: string;
  products?: ProductType[];
  items?: any[];
  items_cost?: number;
  items_revenue?: number;
  avg_cost?: number;

  currency?: string;
  is_paid?: boolean;
  is_paid_dt?: string;
  is_fulfilled_dt?: string;
  total_cost?: number;
  total_cost_data?: PriceDataType;
};

export type ProductSizeType = {
  slug?: string;
  name?: string;
  code?: string;
  quantity?: number;
};
