import * as React from 'react';

import { uuid, getCN } from '@miq/utiljs';

type CommonProps = {
  id?: string;
  className?: string;
};

type DtProps = {
  dt: Date;
  /** Display Week days: short(Mon) long(Monday) */
  short?: string | boolean;
};

export type RenderDayProps = { renderDay?: (props: DayProps) => JSX.Element };

export type MonthProps = DtProps &
  RenderDayProps & {
    activeDt?: Date;
    endDate?: Date;
    children?: React.ReactNode;
    showWeekDay?: string | boolean;
  };

export const Month: React.FC<MonthProps> = (props) => {
  const { dt = new Date(), showWeekDay = true, short, ...rest } = props;

  let monthWeeks = dt.getMonthWeeks();

  return (
    <div className="miq-month">
      {showWeekDay && <WeekDays short={short} />}
      <div id="Weeks" className="miq-month-weeks">
        {monthWeeks.map((w: Date[]) => (
          <Week {...rest} dt={w[0]} showWeekDay={false} key={uuid()} />
        ))}
      </div>
    </div>
  );
};

export type WeekProps = DtProps &
  RenderDayProps & {
    activeDt?: Date;
    children?: React.ReactNode;
    showWeekDay?: string | boolean;
    endDate?: Date;
  };

export const Week: React.FC<WeekProps> = (props: WeekProps) => {
  const { dt = new Date(), endDate, renderDay, ...rest } = props;

  let weekDates: Date[] = dt.getWeekDates();
  if (endDate) weekDates = weekDates.filter((d) => d <= endDate);

  return (
    <div className="miq-week" id="Week">
      <div id="Dates" className="miq-week-dates d-grid grid-md-7">
        {weekDates.map((d: Date) => (
          <Day {...rest} dt={d} render={renderDay} key={uuid()} />
        ))}
      </div>
    </div>
  );
};

export type DayProps = CommonProps &
  DtProps & {
    activeDt?: Date;
    showWeekDay?: string | boolean;
  };

export const Day = (props: DayProps & { render?: (props: DayProps) => JSX.Element }) => {
  const { render, ...rest } = props;

  if (render) return render(rest);

  const { dt, short, showWeekDay = true } = rest;

  return (
    <div className={getCN(['miq-day', props.className])}>
      {showWeekDay && <WeekDay dt={dt} short={short} />}
      <DateNum dt={dt} />
    </div>
  );
};

export type WeekDayProps = DtProps & {};

export const WeekDay = ({ dt, short }: WeekDayProps) => {
  const wd = WEEKDAYS.find((i) => i.index === dt.getDay());
  return <div className="miq-weekday">{short ? wd?.short : wd?.long}</div>;
};

export const WeekDays = ({ short, ...props }: any) => (
  <div className="miq-weekdays d-grid grid-md-7">
    {WEEKDAYS.map((wd) => {
      return (
        <div className="miq-weekday" key={wd.index}>
          {short ? wd?.short : wd?.long}
        </div>
      );
    })}
  </div>
);

export type DateNumProps = CommonProps & DtProps & {};

/** Display Date Number equivalent to date.getDate() */
export const DateNum = ({ dt, className }: DateNumProps) => (
  <div className={getCN(['miq-day-date-num', dt.isToday() && 'today', className])}>{dt?.getDate?.()}</div>
);

export type YearNumProps = DtProps & {};

export const YearNum = ({ dt }: YearNumProps) => {
  return <div className="miq-year-num">{dt.getFullYear()}</div>;
};

export const WEEKDAYS: { index: number; short: string; long: string }[] = [
  { short: 'Sun', long: 'Sunday' },
  { short: 'Mon', long: 'Monday' },
  { short: 'Tue', long: 'Tuesday' },
  { short: 'Wed', long: 'Wednesday' },
  { short: 'Thu', long: 'Thursday' },
  { short: 'Fri', long: 'Friday' },
  { short: 'Sat', long: 'Saturday' },
].map((d, i) => ({ ...d, index: i }));
