import * as React from 'react';

import { Button } from '@miq/componentjs';
import { getCN } from '@miq/utiljs';

import { Month, DayProps, DateNum } from './components';

export type DatepickerProps = {
  activeDt?: Date;
  onDateClick?: (dt?: Date) => void;
  dt: Date;
  id?: string;
  className?: string;
};

export default function DatePicker(props: DatepickerProps) {
  const { onDateClick, ...rest } = props;
  const { dt } = rest;

  return (
    <div className={getCN(['miq-datepicker', props.className])}>
      <Month
        {...props}
        short
        dt={dt}
        renderDay={(args) => <DatePickerDay {...args} onClick={onDateClick} />}
        endDate={dt.getMonthLastDt()}
      />
    </div>
  );
}

type DatePickerDayProps = DayProps & {
  onClick?: (dt?: Date) => void;
};

const DatePickerDay = (props: DatePickerDayProps) => {
  const { dt, activeDt, onClick } = props;
  const isToday = dt.isToday();
  const isActive = dt.isSameDay(activeDt);

  const handleClick = () => {
    if (!onClick) return;
    return onClick(dt);
  };

  return (
    <div className={getCN(['miq-day miq-datepicker-day'])}>
      <Button onClick={handleClick} className={getCN(['miq-datepicker-btn', isToday && 'today', isActive && 'active'])}>
        <DateNum dt={dt} />
      </Button>
    </div>
  );
};
