import * as React from 'react';
import * as Axios from 'axios';
import { Routes, Route } from 'react-router-dom';

import './account.scss';

import Form, { useForm } from '@miq/formjs';

import { SharedDataCtx, SharedDataState } from '@miq/contextjs';

import Staff from '..';
import { StaffModel, StaffService } from '../service';
import { StaffUser } from '../models';

export default function AccountStaffLayout() {
  return (
    <Routes>
      <Route path="/" element={<ProfileUpdateView />} />
    </Routes>
  );
}

//#region UserModel

//#endregion UserModel

//#region ProfileUpdateView

const ProfileUpdateView = () => {
  const { user, updateUser, apps } = React.useContext<SharedDataState>(SharedDataCtx);
  const { employee } = user;

  const formInitData: any = { first_name: user.first_name, last_name: user.last_name, gender: user.gender };
  if (employee) formInitData.position = employee.position;

  const form = useForm(formInitData);

  if (!user) return null;

  const instance = StaffUser(user);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    return instance
      .update(form.values)
      .then(({ data }: Axios.AxiosResponse) => updateUser(data))
      .catch((err: Axios.AxiosResponse) => form.handleError(err));
  };

  return (
    <Staff.View title="Account">
      <Form context={form} onSubmit={handleSubmit}>
        <div className="d-grid grid-md-4 gap-2">
          <Staff.Section
            title="Profile information"
            text="Update your profile information."
            className="span-md-3"
            footer={<Form.Submit value="Save" />}
          >
            <Form.Field label="First name" error={form.errors.first_name} className="d-grid grid-md-5">
              <Form.Text
                required
                name="first_name"
                // onSave={saveAccount}
                // isValid={validate}
                placeholder="First name"
                className="span-md-3"
                minLength={2}
              />
            </Form.Field>

            <Form.Field label="Last name" error={form.errors.last_name} className="d-grid grid-md-5">
              <Form.Text
                required
                name="last_name"
                // onSave={saveAccount}
                // isValid={validate}
                placeholder="Last name"
                className="span-md-3"
                minLength={2}
              />
            </Form.Field>

            <Form.Field label="Gender" className="d-grid grid-md-5">
              <Form.Select
                required
                name="gender"
                nullValue={{ label: 'Select gender' }}
                // onChange={({ target }) => saveAccount(target)}
                className="span-md-3"
              >
                <option value="OTHER">Other</option>
                <option value="MALE">Male</option>
                <option value="FEMALE">Female</option>
              </Form.Select>
            </Form.Field>

            {
              //#region HRM
            }
            {Object.keys(apps).includes('miq_hrm') && (
              <Form.Field label="Role" error={form.errors.position} className="d-grid grid-md-5 mb-1">
                <Form.Text
                  required
                  name="position"
                  // onSave={({ value }) =>
                  //   services
                  //     .patch(
                  //       'account/current/position/',
                  //       { position: value }
                  //       // { position: employee.position }
                  //     )
                  //     .catch((err) => {
                  //       form.handleError(err);
                  //     })
                  // }
                  // isValid={validate}
                  placeholder="Role"
                  className="span-md-3"
                  minLength={2}
                />
              </Form.Field>
            )}
            {
              //#endregion HRM
            }
          </Staff.Section>

          <Staff.Section
            title="Avatar"
            text="Update your profile picture. Images must be .png, .jpg or .gif format."
            // actions={
            //   <ImgDeleteIconButton
            //     slug={user.img}
            //     onSuccess={() => updateUser({ img: null, img_data: null })}
            //     // onError={() => toast.error({ message: 'Something went wrong.' })}
            //     label=""
            //     className="btn-danger-3"
            //   />
            // }
            className="avatar-section"
          >
            {/* <div className="d-flex">{user.img ? <Avatar {...user.img_data} /> : <AvatarUploadButton />}</div> */}
          </Staff.Section>
        </div>

        <Staff.Section title="Email" text="Please contact support to change your email.">
          <p className="text-muted text-sm">{user.email}</p>
        </Staff.Section>

        <Staff.Section
          title="Password"
          text="Please contact support to change your password. Password must be at least 6 characters long."
        />
      </Form>
    </Staff.View>
  );
};

//#endregion ProfileUpdateView
