import * as React from 'react';

import './calendar.scss';

import { Button } from '@miq/componentjs';
import { DateNum, Day, Week, WeekDays, WeekDay, YearNum } from './components';
import DatePicker from './datepicker';

//#region Calendar

type CalendarProps = {
  currentDate?: Date;
};

export default function Calendar(props?: CalendarProps) {
  const currentDate: Date = props?.currentDate || new Date();

  return Object.assign({}, props, { currentDate });
}

Calendar.DatePicker = DatePicker;

Calendar.Week = Week;
Calendar.WeekDay = WeekDay;
Calendar.WeekDays = WeekDays;
Calendar.Day = Day;

Calendar.DateNum = DateNum;
Calendar.YearNum = YearNum;

//#endregion Calendar

//#region BUTTONS

export type CalendarButtonProps = {
  children: React.ReactNode;
  dt: Date;
  onClick: (dt: Date) => void;
};

Calendar.PrevWeekButton = ({ children, dt, ...props }: CalendarButtonProps) => {
  return (
    <Button {...props} onClick={() => props?.onClick(dt.subDays(7))}>
      {children}
    </Button>
  );
};
Calendar.NextWeekButton = ({ children, dt, ...props }: CalendarButtonProps) => {
  return (
    <Button {...props} onClick={() => props?.onClick(dt.addDays(7))}>
      {children}
    </Button>
  );
};

//#endregion BUTTONS
